import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { IonicComponentsService } from '../services/ionic-components.service';
import { Router } from '@angular/router';
import { StorageService } from '../services/storage.service';
import { environment as env } from 'src/environments/environment';
import { Funciones } from '../compartido/funciones';
import { AppComponent } from '../app.component';

import { AuthService } from '../services/auth.service';
import { UserService } from '../services/user.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {

  loginForm: FormGroup;
  viewPass = false;
  tipo = "password";
  name_prueba = 'null';

  constructor(
    public _auth: AuthService,
    private _user: UserService,
    private _fun: Funciones,
    private _storage: StorageService,
    public router: Router,
    public formBuilder: FormBuilder,
    public _comp: IonicComponentsService) {
    this.initForm();
    this.name_prueba = env.NOMBRE_APP;
    console.log('this.name_prueba',this.name_prueba);
  }


  ngOnInit() { }

  tiggerFields(form) {
    Object.keys(form.controls).forEach(field => {
      let _control = form.get(field);
      if (_control instanceof FormControl)
        _control.markAsTouched({ onlySelf: true });
    });
  }

  validateFormLogin() {
    this.tiggerFields(this.loginForm);
    if (this.loginForm.valid) this.login();
  }

  initForm() {
    this.loginForm = this.formBuilder.group({
      pass: ['', Validators.required],
      userName: ['', Validators.required]
    });
  }

  async login() {

    let loading: any = await this._comp.presentLoading();
    const res_user: any = await this._user.login(this.loginForm.value);
    console.log('res_user', res_user);

    if (!res_user.valid) {
      loading.dismiss();
      await this._fun.alertError(res_user.mensaje);
      return;
    }

    const user = res_user.res;
    this._auth.currentUser.next(user);
    await this._storage.set(env.APP_KEY, user._id);
    loading.dismiss();
    this.router.navigate(['/pedidos']);
  }

  mostrar() {
    if (this.viewPass) {
      this.tipo = "password";
      this.viewPass = false;
    } else {
      this.tipo = "text";
      this.viewPass = true;
    }
  }



}
